@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@500;700&display=swap');

@import 'variables';
@import 'article-component';
@import 'footer';


* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: $primary-ff;
    font-size: $root-fs;
    background-color: $light-grayish-blue;
    display: grid;
    place-items: center;
    height: 100vh;
}

h1, p {
    margin: 0;
}

img {
    width: 100%;
}

// Layouts

// Utilities 

.hide {
    display: none;
}

// Media queries

@media (min-width: 600px) {
    .article-component {
        flex-direction: row;
        
        &__contents-container {
            padding: 2em 2.5em 1.25em;
        }

        &__title {
            font-size: $fs-20px;
            line-height: 1.75rem;
            letter-spacing: 0.01563rem;
            margin-bottom: .6em;
        }

        &__image {
            height: auto;
            object-position: left;
        }

        &__infos-container.share-mobile--light {
            padding: 0 2.5em 2em;
        }

    }
}