.article-component {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    min-width: 327px;
    max-width: 730px;
    border-radius: 0.625rem;
    overflow: hidden;
    background: $white-clr;
    box-shadow: 0px 40px 40px -10px rgba(201, 213, 225, 0.50);

    &__image-wrapper {
        width: 100%;
        display: flex;
    }

    &__image {
        min-width: 285px;
        height: clamp(200px, 43vw, 270px);
        object-fit: cover;
    }

    &__contents-container {
        padding: 2.25rem 2rem 1.25rem;
    }

    &__title {
        color: $very-dark-grayish-blue;
        font-size: $fs-16px;
        font-weight: $fw-bold;
        line-height: 1.5rem;
        letter-spacing: 0.0125rem;
        margin-bottom: .75em;
    }

    &__description {
        color: $desaturated-dark-blue;
        font-size: $fs-13px;
        font-weight: $fw-medium;
        line-height: 1.25rem;
        letter-spacing: 0.00763rem;
    }

    &__infos-container {
        display: flex;
        align-items: center;
        gap: 1em;
    }

    &__infos-container.infos-mobile--light {
        padding: .75em 2em 1.25em;
    }

    &__infos-container.infos-mobile--dark {
        padding: 1em 2em;
    }

    // Share mobile light

    &__avatar {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }

    &__name,
    &__date {
        font-size: $fs-13px;
        line-height: 1.25rem;
        letter-spacing: 0.00763rem;
    }

    &__name {
        color: $very-dark-grayish-blue;
    }

    &__date {
        color: $grayish-blue;
    }

    &__share-icon-wrapper {
        width: 37px;
        height: 37px;
        display: grid;
        place-items: center;
        margin-left: auto;
        border-radius: 50%;
        cursor: pointer;
    }

    &__share-icon-wrapper.share-icon-wrapper--light {
        background-color: $light-grayish-blue;
    }

    &__share-icon-wrapper.share-icon-wrapper--dark {
        background-color: $desaturated-dark-blue;
    }

    &__share-icon {
        width: 15px;
        height: 13px;
    }

    // share mobile dark

    &__infos-container.infos-mobile--dark {
        background-color: $very-dark-grayish-blue;
        
        .social-icons-wrapper {
            display: flex;
            align-items: center;
            gap: 1em;
        }

        .social-media-icon {
            width: 20px;
        }
    }

    &__share-text {
        color: $grayish-blue;
        font-size: $fs-13px;
        text-transform: uppercase;
        line-height: 1.25rem;
        letter-spacing: 0.3125rem;
    }
}